import React, {Component} from 'react';
import PropTypes from 'prop-types';

import 'react-tabulator/lib/styles.css'; // required styles
import 'react-tabulator/lib/css/tabulator.min.css'; // theme
import { ReactTabulator } from 'react-tabulator'

/**
 * ExampleComponent is an example component.
 * It takes a property, `label`, and
 * displays it.
 * It renders an input with the property `value`
 * which is editable by the user.
 */
export default class DashTabulator extends Component {
    ref = null;
    rowClick = (e, row) => {
        //console.log('ref table: ', this.ref.table); // this is the Tabulator table instance
        console.log('rowClick id: ${row.getData().id}', row, e);
        this.props.setProps({rowClicked: row._row.data})
    };
    downloadData = () => {
        this.ref.table.download("xlsx", "data.xlsx");
    };
    render() {
        const {id, data, setProps, columns, options, rowClicked} = this.props;
        
        //options.set({downloadDataFormatter: (data) => data})
        //options.set({downloadReady: (fileContents, blob) => blob})
        const options2 = {...options, 
            downloadDataFormatter: (data) => data,
            downloadReady: (fileContents, blob) => blob
        }
        return (
            <div>
            <button type="button" onClick={this.downloadData} className="btn btn-success" id="download-xlsx">Download XLSX</button>
            <ReactTabulator
                ref={ref => (this.ref = ref)}
                data={data}
                columns={columns}
                tooltips={true}
                layout={"fitData"}
                options={options2}
                rowClick={this.rowClick}
            />
            </div>
        );
    }
}

DashTabulator.defaultProps = {};

DashTabulator.propTypes = {
    /**
     * The ID used to identify this component in Dash callbacks.
     */
    id: PropTypes.string,

    /**
     * A label that will be printed when this component is rendered.
     */
    columns: PropTypes.array,

    /**
     * The value displayed in the input.
     */
    data: PropTypes.array,

    /**
     * Dash-assigned callback that should be called to report property changes
     * to Dash, to make them available for callbacks.
     */
    setProps: PropTypes.func,

    /**
     * Tabulator Options
     */

    options: PropTypes.object,

    
    /**
     * rowClick captures the row that was clicked on
     */
    rowClicked: PropTypes.object,
    
};
